/* =========================================
   1. RESET & CORE VARIABLES
   ========================================= */
:root {
  /* Default (Dark) Theme - Enhanced Cyberpunk Aesthetic */
  --bg-core: #000000; /* Pure Black */
  --bg-subtle: #0d0d0d; /* Slightly lighter black */
  --bg-sidebar: #050505; /* Deep black for sidebar */
  --bg-card: rgba(255, 255, 255, 0.04); /* Subtle glass */
  
  /* Vibrant Neon Accents */
  --accent-primary: #00ffcc; /* Electric Cyan */
  --accent-secondary: #ff00ff; /* Neon Magenta */
  --accent-tertiary: #ffcc00; /* Electric Gold */
  
  /* Text Colors */
  --text-header: #ffffff;
  --text-main: #e8e8e8;
  --text-muted: #777777;
  
  /* Enhanced Glassmorphism */
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-border: rgba(0, 255, 204, 0.12); /* Cyan tint */
  --glass-blur: 15px;
  --shadow-color: rgba(0, 255, 204, 0.2); /* Cyan glow */
  --shadow-hover: rgba(0, 255, 204, 0.35); /* Stronger glow on hover */

  /* Dimensions */
  --sidebar-width: 280px;
  --header-height: 100px;
  --radius-sm: 0px; /* Boxy */
  --radius-md: 0px;
  
  /* Typography */
  --font-main: 'Inter', sans-serif;
  --font-mono: 'JetBrains Mono', monospace;
}

/* Light Theme - Premium Minimal Aesthetic */
.theme-light {
  --bg-core: #fafafa; /* Soft off-white */
  --bg-subtle: #f0f0f0; /* Light grey */
  --bg-sidebar: #ffffff; /* Pure white sidebar */
  --bg-card: rgba(0, 0, 0, 0.02); /* Subtle dark glass */
  
  /* Elegant Dark Accents */
  --accent-primary: #1a1a1a; /* Deep charcoal */
  --accent-secondary: #4a4a4a; /* Medium grey */
  --accent-tertiary: #0066cc; /* Premium blue */
  
  /* Text Colors */
  --text-header: #0a0a0a;
  --text-main: #2a2a2a;
  --text-muted: #666666;
  
  /* Refined Glassmorphism */
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(0, 0, 0, 0.08); /* Soft border */
  --shadow-color: rgba(0, 0, 0, 0.08);
  --shadow-hover: rgba(0, 0, 0, 0.15);
}

/* Light Theme Specific Overrides */
.theme-light .glass-card,
.theme-light .glass-panel,
.theme-light .resource-link,
.theme-light .nav-card,
.theme-light .footer-section {
  border-color: rgba(0,0,0,0.1);
  box-shadow: 0 2px 8px rgba(0,0,0,0.06); /* Soft elevation */
  background: rgba(255,255,255,0.9);
}

.theme-light .glass-card:hover,
.theme-light .glass-panel:hover,
.theme-light .nav-card:hover {
  border-color: var(--accent-primary);
  box-shadow: 0 4px 16px rgba(0,0,0,0.1);
  transform: translateY(-2px);
}

.theme-light .sidebar {
  box-shadow: 2px 0 12px rgba(0,0,0,0.05);
  border-right: 1px solid rgba(0,0,0,0.06);
}

/* Global Reset & Base */
* { box-sizing: border-box; margin: 0; padding: 0; }

body {
  font-family: var(--font-main);
  background-color: var(--bg-core);
  color: var(--text-main);
  overflow-x: hidden;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* =========================================
   PREMIUM UTILITIES & ANIMATIONS
   ========================================= */
/* Glass Card - The "Grey Transparent Box" Standard */
.glass-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(5px);
  padding: 2rem;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.glass-card:hover {
  border-color: var(--accent-primary);
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 15px var(--shadow-color);
  transform: translateY(-2px);
}

/* Glitch Text Effect */
.glitch-text {
  position: relative;
  color: var(--text-header);
}
.glitch-text::before, .glitch-text::after {
  content: attr(data-text);
  position: absolute; top: 0; left: 0; width: 100%; height: 100%;
}
.glitch-text::before {
  left: 2px; text-shadow: -1px 0 red; clip: rect(24px, 550px, 90px, 0);
  animation: glitch-anim 3s infinite linear alternate-reverse;
}
.glitch-text::after {
  left: -2px; text-shadow: -1px 0 blue; clip: rect(85px, 550px, 140px, 0);
  animation: glitch-anim 2s infinite linear alternate-reverse;
}

@keyframes glitch-anim {
  0% { clip: rect(10px, 9999px, 30px, 0); }
  20% { clip: rect(80px, 9999px, 100px, 0); }
  40% { clip: rect(40px, 9999px, 60px, 0); }
  60% { clip: rect(120px, 9999px, 140px, 0); }
  80% { clip: rect(20px, 9999px, 40px, 0); }
  100% { clip: rect(60px, 9999px, 80px, 0); }
}

/* Scanline Overlay */
.scanlines {
  position: fixed; top: 0; left: 0; width: 100%; height: 100%; pointer-events: none; z-index: 9999;
  background: linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0) 50%, rgba(0,0,0,0.1) 50%, rgba(0,0,0,0.1));
  background-size: 100% 4px;
  animation: scanlineMove 0.5s linear infinite;
  opacity: 0.3;
}
@keyframes scanlineMove { from { background-position: 0 0; } to { background-position: 0 4px; } }

/* =========================================
   LAYOUT
   ========================================= */
.dashboard-layout { display: flex; min-height: 100vh; }

/* =========================================
   2. HEADER
   ========================================= */
.main-header {
  position: fixed;
  top: 0;
  left: var(--sidebar-width);
  right: 0;
  height: var(--header-height);
  background: var(--bg-header);
  border-bottom: var(--glass-border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3rem;
  z-index: 900;
  backdrop-filter: blur(10px);
}

.header-title {
  font-family: var(--font-mono);
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-header);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.theme-switcher {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--bg-subtle);
  padding: 0.3rem;
  border: var(--glass-border);
}

.theme-btn {
  background: transparent;
  border: none;
  color: var(--text-muted);
  padding: 0.4rem 0.8rem;
  font-family: var(--font-mono);
  font-size: 0.8rem;
  cursor: pointer;
  display: flex; align-items: center; gap: 0.5rem;
  transition: all 0.2s;
}

.theme-btn:hover { color: var(--text-main); }

.theme-btn.active {
  background: var(--bg-core);
  color: var(--accent-primary);
  border: 1px solid var(--accent-primary);
  font-weight: bold;
}

.theme-divider { width: 1px; height: 15px; background: var(--glass-border); }

/* =========================================
   3. SIDEBAR NAVIGATION
   ========================================= */
/* =========================================
   3. SIDEBAR NAVIGATION
   ========================================= */
.sidebar {
  position: sticky; /* Changed from fixed to sticky for better layout integration */
  top: 0;
  width: var(--sidebar-width);
  height: 100vh;
  background: var(--bg-sidebar);
  border-right: var(--glass-border);
  padding: 2.5rem 2rem;
  display: flex;
  flex-direction: column;
  z-index: 1000;
  box-shadow: 5px 0 20px var(--shadow-color);
  transition: background 0.3s ease, border-color 0.3s ease;
  flex-shrink: 0; /* Prevent shrinking */
}

.sidebar-header { margin-bottom: 3rem; text-align: center; }
.brand-logo {
  font-family: var(--font-mono);
  font-size: 2rem;
  font-weight: 800;
  letter-spacing: -1.5px;
  background: linear-gradient(135deg, var(--text-main) 0%, var(--accent-primary) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.brand-sub {
  color: var(--text-muted);
  font-size: 0.65rem;
  letter-spacing: 3px;
  text-transform: uppercase;
  margin-top: 0.5rem;
  opacity: 0.8;
}

.sidebar-nav { flex-grow: 1; display: flex; flex-direction: column; justify-content: flex-start; }
.nav-list { list-style: none; display: flex; flex-direction: column; gap: 0; /* No gap for boxy list */ }

.nav-item {
  display: flex; align-items: center; gap: 1rem;
  padding: 1rem 1.2rem;
  color: var(--text-muted);
  border-radius: var(--radius-sm); /* Boxy */
  text-decoration: none;
  transition: all 0.2s;
  font-weight: 500;
  cursor: pointer;
  border-left: 3px solid transparent;
}

.nav-item:hover, .nav-item.active {
  color: var(--text-main);
  background: var(--bg-subtle);
  border-color: var(--accent-primary);
}

/* Submenu Styles */
.submenu-list {
  list-style: none;
  padding-left: 0;
  margin-left: 0;
  background: var(--bg-subtle);
  border-left: 3px solid var(--accent-primary); /* Connected boxy look */
}

.submenu-item {
  display: block;
  padding: 0.8rem 1rem 0.8rem 2.5rem;
  color: var(--text-muted);
  font-size: 0.85rem;
  text-decoration: none;
  transition: all 0.2s;
  border-radius: var(--radius-sm);
}

.submenu-item:hover { color: var(--text-main); background: rgba(0,0,0,0.05); }
.submenu-item.active { color: var(--accent-primary); font-weight: 600; background: rgba(0,0,0,0.05); }

.sidebar-footer {
  margin-top: auto;
  padding-top: 2rem;
  border-top: var(--glass-border);
  text-align: center;
}
.connect-label {
  font-size: 0.7rem;
  color: var(--text-muted);
  text-transform: uppercase;
  margin-bottom: 1.2rem;
  letter-spacing: 1.5px;
  opacity: 0.6;
}
.social-dock {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.social-icon {
  color: var(--text-muted);
  transition: all 0.2s;
  font-size: 1.2rem;
  padding: 8px;
  border-radius: 0; /* Boxy Icons maybe? User said "kotak kotak". Let's keep icons uniform but container square? Icons usually circle. Let's make button square */
  border: 1px solid var(--glass-border);
}
.social-icon:hover {
  color: var(--accent-primary);
  border-color: var(--accent-primary);
  background: var(--bg-subtle);
}

/* =========================================
   4. MAIN CONTENT AREA
   ========================================= */
.main-content {
  /* margin-left: var(--sidebar-width); REMOVED - Using Flexbox */
  /* width: calc(100% - var(--sidebar-width)); REMOVED - Using Flexbox */
  flex-grow: 1; /* Take remaining space */
  min-height: 100vh;
  /* Removed padding-top here as header handles it */
  display: flex; flex-direction: column;
  background-color: var(--bg-core);
}

.content-scrollable {
  flex-grow: 1;
  padding: 5rem 2rem 2rem 2rem; /* Added top padding per user request ("agak kebawah") */
  width: 100%;
  max-width: 1200px; /* Limit width for better readability */
  margin: 0 auto;
}

/* =========================================
   5. PAGE WRAPPER & DOCS SCALING
   ========================================= */
.page-wrapper {
  padding: 0;
  width: 100%;
}

/* Docs Specific Scaling - "Normal Aja" */
.page-header h1 {
  font-size: 2.5rem; /* Reduced from potential 3rem+ */
  margin-bottom: 0.5rem;
  color: var(--text-header);
}
.page-header p {
  font-size: 1rem; /* Normal reading size */
  color: var(--text-muted);
  max-width: 800px;
}

/* Ensure command tables aren't massive */
.command-table th, .command-table td {
  padding: 1rem;
  font-size: 0.95rem; /* Normal size */
}

/* General Text Normalization */
p, li {
  font-size: 1rem;
  line-height: 1.6;
}

/* Typography & Text */
p { color: var(--text-muted); margin-bottom: 1.2rem; font-size: 1.05rem; }
strong { color: var(--text-main); font-weight: 600; }
h2, h3, h4 { color: var(--text-main); font-weight: 700; margin-top: 2.5rem; margin-bottom: 1rem; letter-spacing: -0.5px; }

/* Buttons & CTA (Boxy Premium) */
.cta-group { display: flex; gap: 1rem; margin-top: 2.5rem; }

.btn-primary {
  background: var(--accent-primary);
  color: #000;
  padding: 1rem 2.2rem;
  border-radius: var(--radius-sm); /* Boxy */
  text-decoration: none;
  font-weight: 700;
  display: inline-flex; align-items: center; gap: 0.5rem;
  transition: all 0.2s;
  border: 1px solid var(--accent-primary);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
}
.btn-primary:hover {
  background: transparent;
  color: var(--accent-primary);
  box-shadow: 4px 4px 0 var(--accent-primary); /* Boxy Shadow */
  transform: translate(-2px, -2px);
}

.btn-secondary {
  background: transparent;
  border: 1px solid var(--text-muted);
  color: var(--text-main);
  padding: 1rem 2.2rem;
  border-radius: var(--radius-sm); /* Boxy */
  text-decoration: none;
  font-weight: 600;
  display: inline-flex; align-items: center; gap: 0.5rem;
  transition: all 0.2s;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
}
.btn-secondary:hover {
  border-color: var(--text-main);
  background: var(--bg-subtle);
  box-shadow: 4px 4px 0 var(--text-muted);
  transform: translate(-2px, -2px);
}

.btn-sm { padding: 0.6rem 1.2rem; font-size: 0.8rem; }

/* Tech Tags - Boxy */
.tech-stack-container { display: flex; flex-wrap: wrap; gap: 0.5rem; margin: 1.5rem 0 3rem 0; }
.tech-tag {
  border: 1px solid var(--glass-border);
  color: var(--accent-primary);
  background: var(--bg-subtle);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-sm); /* Boxy */
  font-size: 0.85rem;
  font-weight: 500;
  font-family: var(--font-mono);
}
.tech-tag:hover {
  border-color: var(--accent-primary);
  background: var(--bg-core);
}

/* Cards & Glass Panels - Boxy */
.glass-panel {
  background: var(--bg-card);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: var(--glass-border);
  border-radius: var(--radius-md); /* Boxy */
  box-shadow: 0 4px 20px var(--shadow-color);
  transition: all 0.2s;
}
.glass-panel:hover {
  border-color: var(--accent-primary);
  transform: translateY(-2px);
  box-shadow: 6px 6px 0 var(--shadow-color); /* Retro boxy shadow */
}

.bio-clean-container {
  border-left: 4px solid var(--accent-primary);
  background: var(--bg-subtle);
  padding: 2rem;
  border-radius: var(--radius-sm);
  margin-bottom: 3rem;
}

.code-block {
  background: #000;
  padding: 1.5rem;
  border-radius: var(--radius-sm);
  font-family: var(--font-mono);
  font-size: 0.9rem;
  overflow-x: auto;
  color: var(--accent-primary);
  border: 1px solid var(--glass-border);
}

/* Tables */
table { width: 100%; border-collapse: collapse; }
th { text-align: left; padding: 1.2rem; color: var(--text-header); border-bottom: 2px solid var(--glass-border); font-weight: 700; text-transform: uppercase; font-size: 0.8rem; letter-spacing: 1px; }
td { padding: 1.2rem; color: var(--text-muted); border-bottom: 1px solid var(--glass-border); transition: background 0.2s; }
tr:hover td { background: var(--bg-subtle); color: var(--text-main); }

/* =========================================
   5. HOME / HERO SECTION
   ========================================= */
/* =========================================
   5. HOME / HERO SECTION
   ========================================= */
.hero-section {
  display: flex; align-items: center; justify-content: center; /* Centered */
  min-height: 70vh; position: relative;
  text-align: center; /* Center text */
}

.content-container { 
  z-index: 2; max-width: 900px; width: 100%;
  display: flex; flex-direction: column; align-items: center; /* Center children */
}

.intro-text {
  font-family: var(--font-mono);
  color: var(--accent-primary);
  font-size: 1rem;
  margin-bottom: 1.5rem;
  display: flex; align-items: center; justify-content: center; gap: 0.8rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 700;
}
.highlight-text {
  color: var(--text-header);
  background: var(--accent-primary);
  color: #000;
  padding: 0 5px;
}

.main-headline {
  font-size: 4.5rem;
  font-weight: 900;
  line-height: 1;
  color: var(--text-header);
  margin-bottom: 2rem;
  letter-spacing: -2px;
  text-transform: uppercase;
}
.typing-effect {
  color: var(--accent-primary);
  border-right: 10px solid var(--accent-primary); /* Block cursor */
  padding-right: 5px;
  animation: blinkCursor 0.8s steps(2) infinite;
}

.sub-headline {
  font-size: 1.3rem;
  color: var(--text-muted);
  font-weight: 400;
  line-height: 1.6;
  max-width: 700px;
  margin-bottom: 3rem;
  /* Removed border-left for symmetrical center alignment */
  padding-left: 0;
}

@keyframes blinkCursor {
  0%, 100% { border-color: transparent; }
  50% { border-color: var(--accent-primary); }
}

/* =========================================
   6. DOCS FOOTER & UTILS (SYMMETRICAL FIX)
   ========================================= */
.docs-footer-container {
  margin-top: 5rem;
  border-top: 1px solid var(--glass-border);
  padding-top: 3rem;
}

.docs-navigation {
  display: flex; justify-content: space-between; gap: 2rem;
  margin-bottom: 4rem;
}

.nav-card {
  padding: 1.5rem;
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-sm);
  text-decoration: none;
  flex: 1;
  background: var(--bg-subtle);
  transition: all 0.2s;
  display: flex; flex-direction: column;
}
.nav-card:hover {
  border-color: var(--accent-primary);
  background: var(--bg-card);
  box-shadow: 4px 4px 0 var(--accent-primary);
  transform: translate(-2px, -2px);
}

.nav-label { font-size: 0.85rem; color: var(--text-muted); display: flex; align-items: center; gap: 0.5rem; margin-bottom: 0.6rem; text-transform: uppercase; letter-spacing: 1px; font-weight: 700; }
.nav-card.next { text-align: right; align-items: flex-end; }
.nav-card.next .nav-label { justify-content: flex-end; }
.nav-title { color: var(--text-main); font-weight: 700; font-size: 1.3rem; }

/* Centered Footer Grid */
.footer-main-heading { font-size: 2rem; text-align: center; margin-bottom: 3rem; color: var(--text-header); }

.footer-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  margin-top: 1rem;
  align-items: stretch; /* Stretch to fill height */
}

.footer-section {
  padding: 2rem;
  /* Boxy grey transparent look as requested */
  background: rgba(255, 255, 255, 0.05); 
  border: 1px solid rgba(255, 255, 255, 0.1); 
  text-align: center;
  transition: all 0.2s;
  height: 100%;
}
.footer-section:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: var(--accent-primary);
}

.footer-subheading { color: var(--text-main); font-size: 1.1rem; margin-bottom: 1.5rem; min-height: 2rem; font-weight: 700; }

.feedback-buttons { display: flex; gap: 1rem; justify-content: center; }
.footer-links { list-style: none; padding: 0; display: flex; justify-content: center; gap: 1.5rem; flex-wrap: wrap; } 
/* Changed from column to row for icons */

.contact-icon-btn {
  display: flex; align-items: center; justify-content: center;
  width: 50px; height: 50px;
  background: var(--bg-core);
  border: 1px solid var(--glass-border);
  color: var(--text-muted);
  font-size: 1.5rem;
  transition: all 0.2s;
  cursor: pointer;
  text-decoration: none;
}
.contact-icon-btn:hover {
  background: var(--accent-primary);
  color: #000;
  border-color: var(--accent-primary);
  box-shadow: 3px 3px 0 var(--shadow-color);
  transform: translate(-2px, -2px);
}

/* Disclaimer Footer */
.copyright-bar {
  margin-top: 4rem;
  border-top: 1px solid var(--glass-border);
  padding-top: 2rem;
  text-align: center;
  font-size: 0.9rem;
  color: var(--text-muted);
  opacity: 0.6;
}

/* =========================================
   7. UTILS & TABLE ALIGNMENT (NEW)
   ========================================= */
.command-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 2rem;
}
.command-table th {
  text-align: left;
  padding: 1rem;
  border-bottom: 2px solid var(--glass-border);
  color: var(--text-header);
  font-weight: 700;
  background: var(--bg-subtle);
}
.command-table td {
  padding: 1rem;
  border-bottom: 1px solid var(--glass-border);
  color: var(--text-muted);
  vertical-align: top; /* Align top for descriptions */
}
.command-table tr:last-child td { border-bottom: none; }
.command-table tr:hover td { background: var(--bg-subtle); color: var(--text-main); }

.col-command { width: 30%; font-family: var(--font-mono); color: var(--accent-secondary); font-weight: 600; }
.col-desc { width: 70%; line-height: 1.6; }

/* Animations */
@keyframes fadeIn { from { opacity: 0; transform: translateY(-5px); } to { opacity: 1; transform: translateY(0); } }
.fade-in { animation: fadeIn 0.4s ease forwards; }

/* =========================================
   8. GLOBAL FOOTER (NEW)
   ========================================= */
.global-footer {
  margin-top: 5rem;
  border-top: 1px solid var(--glass-border);
  padding-top: 3rem;
  padding-bottom: 2rem;
}

.footer-link-small {
  display: block;
  font-size: 0.8rem;
  color: var(--accent-primary);
  margin-top: 1rem;
  text-decoration: none;
}
.footer-link-small:hover { text-decoration: underline; }

.footer-links-vertical {
  display: flex; flex-direction: column; gap: 0.8rem; align-items: center; justify-content: center;
}

.footer-link-icon {
  display: flex; align-items: center; gap: 0.5rem;
  color: var(--accent-primary);
  text-decoration: none;
  font-size: 0.95rem;
  font-weight: 500;
}
.footer-link-icon:hover { text-decoration: underline; }

.legal-links {
  display: flex; gap: 1.5rem; justify-content: center; flex-wrap: wrap;
  font-size: 0.8rem;
  color: var(--text-muted);
}
.legal-links a { color: var(--text-muted); text-decoration: none; }
.legal-links a:hover { color: var(--accent-primary); text-decoration: underline; }

/* =========================================
   9. ADVANCED LANGUAGE SELECTOR
   ========================================= */
.language-selector {
  position: relative;
}

.lang-trigger {
  background: var(--bg-card) !important;
  backdrop-filter: blur(15px);
  border: 1px solid var(--glass-border) !important;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.lang-trigger:hover {
  border-color: var(--accent-primary) !important;
  box-shadow: 0 0 20px var(--shadow-hover);
  transform: translateY(-1px);
}

.lang-dropdown-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: transparent;
}

.lang-dropdown {
  position: absolute;
  top: calc(100% + 0.5rem);
  right: 0;
  min-width: 200px;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 32px var(--shadow-color);
  animation: slideDown 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  z-index: 1000;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.lang-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.9rem 1.2rem;
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 1px solid var(--glass-border);
  color: var(--text-main);
  font-family: var(--font-main);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s;
  text-align: left;
}

.lang-option:last-child {
  border-bottom: none;
}

.lang-option:hover {
  background: var(--accent-primary);
  color: #000;
}

.lang-option.active {
  background: rgba(0, 255, 204, 0.1);
  color: var(--accent-primary);
  font-weight: 600;
}

.lang-flag {
  font-size: 1.3rem;
  flex-shrink: 0;
}

.lang-name {
  flex-grow: 1;
}

.check-mark {
  color: var(--accent-primary);
  font-weight: bold;
  font-size: 1.1rem;
}

/* Light theme overrides for dropdown */
.theme-light .lang-dropdown {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 8px 24px rgba(0,0,0,0.12);
}

.theme-light .lang-option:hover {
  background: var(--accent-primary);
  color: #fff;
}

.theme-light .lang-option.active {
  background: rgba(26, 26, 26, 0.08);
  color: var(--accent-primary);
}

/* =========================================
   10. MACOS-STYLE WELCOME SCREEN
   ========================================= */
.welcome-macos {
  position: fixed;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 99999;
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.welcome-macos.fade-in {
  opacity: 1;
}

/* macOS-style gradient background */
.macos-gradient-bg {
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, 
    #667eea 0%, 
    #764ba2 25%,
    #f093fb 50%,
    #4facfe 75%,
    #00f2fe 100%
  );
  animation: gradientShift 15s ease infinite;
}

@keyframes gradientShift {
  0%, 100% { filter: hue-rotate(0deg); }
  50% { filter: hue-rotate(45deg); }
}

/* Floating orbs for depth (like macOS Big Sur+) */
.orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.6;
  animation: float 20s ease-in-out infinite;
}

.orb-1 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(0,255,204,0.4) 0%, transparent 70%);
  top: -10%;
  left: -10%;
  animation-delay: 0s;
}

.orb-2 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(102,126,234,0.4) 0%, transparent 70%);
  bottom: -15%;
  right: -15%;
  animation-delay: -7s;
}

.orb-3 {
  width: 350px;
  height: 350px;
  background: radial-gradient(circle, rgba(240,147,251,0.3) 0%, transparent 70%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation-delay: -14s;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0) scale(1); }
  25% { transform: translate(30px, -30px) scale(1.1); }
  50% { transform: translate(-20px, 20px) scale(0.9); }
  75% { transform: translate(20px, 30px) scale(1.05); }
}

/* Main content card with glassmorphism */
.welcome-card-macos {
  position: relative;
  z-index: 10;
  max-width: 480px;
  padding: 4rem 3rem;
  text-align: center;
  
  /* Frosted glass effect */
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(40px) saturate(180%);
  -webkit-backdrop-filter: blur(40px) saturate(180%);
  
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 
    0 8px 32px 0 rgba(0, 0, 0, 0.2),
    0 0 0 1px rgba(255, 255, 255, 0.1) inset;
  
  animation: cardFadeIn 0.6s cubic-bezier(0.4, 0, 0.2, 1) 0.2s both;
}

@keyframes cardFadeIn {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Icon with gradient */
.welcome-icon-macos {
  margin: 0 auto 2rem;
  animation: iconPulse 3s ease-in-out infinite;
}

@keyframes iconPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* Typography - macOS style */
.welcome-title-macos {
  font-size: 3rem;
  font-weight: 700;
  background: linear-gradient(135deg, #fff 0%, rgba(255,255,255,0.8) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
  letter-spacing: -1px;
}

.welcome-subtitle-macos {
  font-size: 1.5rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
}

.welcome-desc-macos {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 3rem;
  font-weight: 400;
}

/* Continue button - macOS style */
.welcome-button-macos {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2.5rem;
  
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  
  color: #fff;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.welcome-button-macos:hover {
  background: rgba(255, 255, 255, 0.25);
  border-color: rgba(255, 255, 255, 0.4);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
}

.welcome-button-macos:active {
  transform: translateY(0);
}

.welcome-button-macos svg {
  transition: transform 0.3s;
}

.welcome-button-macos:hover svg {
  transform: translateX(4px);
}

/* Footnote */
.welcome-footnote-macos {
  margin-top: 2rem;
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.4);
  font-weight: 400;
}

/* Responsive */
@media (max-width: 600px) {
  .welcome-card-macos {
    padding: 3rem 2rem;
    margin: 1rem;
  }
  
  .welcome-title-macos {
    font-size: 2.25rem;
  }
  
  .welcome-subtitle-macos {
    font-size: 1.25rem;
  }
}

/* Mobile */
@media (max-width: 900px) {
  .footer-grid { grid-template-columns: 1fr; gap: 2rem; }
  .sidebar { width: 0; padding: 0; overflow: hidden; }
  .main-content { margin-left: 0; padding: 2rem; padding-top: calc(var(--header-height) + 2rem); }
  .main-headline { font-size: 2.5rem; }
  .main-header { left: 0; padding: 0 1rem; }
  .col-command { width: 40%; }
  .col-desc { width: 60%; }
  .language-selector { position: fixed; top: 1rem; right: 1rem; }
}
