:root {
  --bg-core: #000;
  --bg-subtle: #0d0d0d;
  --bg-sidebar: #050505;
  --bg-card: #ffffff0a;
  --accent-primary: #0fc;
  --accent-secondary: #f0f;
  --accent-tertiary: #fc0;
  --text-header: #fff;
  --text-main: #e8e8e8;
  --text-muted: #777;
  --glass-bg: #ffffff0d;
  --glass-border: #00ffcc1f;
  --glass-blur: 15px;
  --shadow-color: #0fc3;
  --shadow-hover: #00ffcc59;
  --sidebar-width: 280px;
  --header-height: 100px;
  --radius-sm: 0px;
  --radius-md: 0px;
  --font-main: "Inter", sans-serif;
  --font-mono: "JetBrains Mono", monospace;
}

.theme-light {
  --bg-core: #fafafa;
  --bg-subtle: #f0f0f0;
  --bg-sidebar: #fff;
  --bg-card: #00000005;
  --accent-primary: #1a1a1a;
  --accent-secondary: #4a4a4a;
  --accent-tertiary: #06c;
  --text-header: #0a0a0a;
  --text-main: #2a2a2a;
  --text-muted: #666;
  --glass-bg: #ffffffd9;
  --glass-border: #00000014;
  --shadow-color: #00000014;
  --shadow-hover: #00000026;
}

.theme-light .glass-card, .theme-light .glass-panel, .theme-light .resource-link, .theme-light .nav-card, .theme-light .footer-section {
  background: #ffffffe6;
  border-color: #0000001a;
  box-shadow: 0 2px 8px #0000000f;
}

.theme-light .glass-card:hover, .theme-light .glass-panel:hover, .theme-light .nav-card:hover {
  border-color: var(--accent-primary);
  transform: translateY(-2px);
  box-shadow: 0 4px 16px #0000001a;
}

.theme-light .sidebar {
  border-right: 1px solid #0000000f;
  box-shadow: 2px 0 12px #0000000d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-main);
  background-color: var(--bg-core);
  color: var(--text-main);
  transition: background-color .3s, color .3s;
  overflow-x: hidden;
}

.glass-card {
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(5px);
  background: #ffffff08;
  padding: 2rem;
  transition: all .3s cubic-bezier(.25, .8, .25, 1);
}

.glass-card:hover {
  border-color: var(--accent-primary);
  box-shadow: 0 0 15px var(--shadow-color);
  background: #ffffff0d;
  transform: translateY(-2px);
}

.glitch-text {
  color: var(--text-header);
  position: relative;
}

.glitch-text:before, .glitch-text:after {
  content: attr(data-text);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.glitch-text:before {
  text-shadow: -1px 0 red;
  clip: rect(24px, 550px, 90px, 0);
  animation: 3s linear infinite alternate-reverse glitch-anim;
  left: 2px;
}

.glitch-text:after {
  text-shadow: -1px 0 #00f;
  clip: rect(85px, 550px, 140px, 0);
  animation: 2s linear infinite alternate-reverse glitch-anim;
  left: -2px;
}

@keyframes glitch-anim {
  0% {
    clip: rect(10px, 9999px, 30px, 0);
  }

  20% {
    clip: rect(80px, 9999px, 100px, 0);
  }

  40% {
    clip: rect(40px, 9999px, 60px, 0);
  }

  60% {
    clip: rect(120px, 9999px, 140px, 0);
  }

  80% {
    clip: rect(20px, 9999px, 40px, 0);
  }

  100% {
    clip: rect(60px, 9999px, 80px, 0);
  }
}

.scanlines {
  pointer-events: none;
  z-index: 9999;
  opacity: .3;
  background: linear-gradient(#fff0, #fff0 50%, #0000001a 50%, #0000001a) 0 0 / 100% 4px;
  width: 100%;
  height: 100%;
  animation: .5s linear infinite scanlineMove;
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes scanlineMove {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 0 4px;
  }
}

.dashboard-layout {
  min-height: 100vh;
  display: flex;
}

.main-header {
  top: 0;
  left: var(--sidebar-width);
  height: var(--header-height);
  background: var(--bg-header);
  border-bottom: var(--glass-border);
  z-index: 900;
  backdrop-filter: blur(10px);
  justify-content: space-between;
  align-items: center;
  padding: 0 3rem;
  display: flex;
  position: fixed;
  right: 0;
}

.header-title {
  font-family: var(--font-mono);
  color: var(--text-header);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.2rem;
  font-weight: 700;
}

.theme-switcher {
  background: var(--bg-subtle);
  border: var(--glass-border);
  align-items: center;
  gap: .5rem;
  padding: .3rem;
  display: flex;
}

.theme-btn {
  color: var(--text-muted);
  font-family: var(--font-mono);
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  gap: .5rem;
  padding: .4rem .8rem;
  font-size: .8rem;
  transition: all .2s;
  display: flex;
}

.theme-btn:hover {
  color: var(--text-main);
}

.theme-btn.active {
  background: var(--bg-core);
  color: var(--accent-primary);
  border: 1px solid var(--accent-primary);
  font-weight: bold;
}

.theme-divider {
  background: var(--glass-border);
  width: 1px;
  height: 15px;
}

.sidebar {
  width: var(--sidebar-width);
  background: var(--bg-sidebar);
  border-right: var(--glass-border);
  z-index: 1000;
  height: 100vh;
  box-shadow: 5px 0 20px var(--shadow-color);
  flex-direction: column;
  flex-shrink: 0;
  padding: 2.5rem 2rem;
  transition: background .3s, border-color .3s;
  display: flex;
  position: sticky;
  top: 0;
}

.sidebar-header {
  text-align: center;
  margin-bottom: 3rem;
}

.brand-logo {
  font-family: var(--font-mono);
  letter-spacing: -1.5px;
  background: linear-gradient(135deg, var(--text-main) 0%, var(--accent-primary) 100%);
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 2rem;
  font-weight: 800;
}

.brand-sub {
  color: var(--text-muted);
  letter-spacing: 3px;
  text-transform: uppercase;
  opacity: .8;
  margin-top: .5rem;
  font-size: .65rem;
}

.sidebar-nav {
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start;
  display: flex;
}

.nav-list {
  flex-direction: column;
  gap: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  color: var(--text-muted);
  border-radius: var(--radius-sm);
  cursor: pointer;
  border-left: 3px solid #0000;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.2rem;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.nav-item:hover, .nav-item.active {
  color: var(--text-main);
  background: var(--bg-subtle);
  border-color: var(--accent-primary);
}

.submenu-list {
  background: var(--bg-subtle);
  border-left: 3px solid var(--accent-primary);
  margin-left: 0;
  padding-left: 0;
  list-style: none;
}

.submenu-item {
  color: var(--text-muted);
  border-radius: var(--radius-sm);
  padding: .8rem 1rem .8rem 2.5rem;
  font-size: .85rem;
  text-decoration: none;
  transition: all .2s;
  display: block;
}

.submenu-item:hover {
  color: var(--text-main);
  background: #0000000d;
}

.submenu-item.active {
  color: var(--accent-primary);
  background: #0000000d;
  font-weight: 600;
}

.sidebar-footer {
  border-top: var(--glass-border);
  text-align: center;
  margin-top: auto;
  padding-top: 2rem;
}

.connect-label {
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: .6;
  margin-bottom: 1.2rem;
  font-size: .7rem;
}

.social-dock {
  justify-content: center;
  gap: 1.5rem;
  display: flex;
}

.social-icon {
  color: var(--text-muted);
  border: 1px solid var(--glass-border);
  border-radius: 0;
  padding: 8px;
  font-size: 1.2rem;
  transition: all .2s;
}

.social-icon:hover {
  color: var(--accent-primary);
  border-color: var(--accent-primary);
  background: var(--bg-subtle);
}

.main-content {
  background-color: var(--bg-core);
  flex-direction: column;
  flex-grow: 1;
  min-height: 100vh;
  display: flex;
}

.content-scrollable {
  flex-grow: 1;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 5rem 2rem 2rem;
}

.page-wrapper {
  width: 100%;
  padding: 0;
}

.page-header h1 {
  color: var(--text-header);
  margin-bottom: .5rem;
  font-size: 2.5rem;
}

.page-header p {
  color: var(--text-muted);
  max-width: 800px;
  font-size: 1rem;
}

.command-table th, .command-table td {
  padding: 1rem;
  font-size: .95rem;
}

p, li {
  font-size: 1rem;
  line-height: 1.6;
}

p {
  color: var(--text-muted);
  margin-bottom: 1.2rem;
  font-size: 1.05rem;
}

strong {
  color: var(--text-main);
  font-weight: 600;
}

h2, h3, h4 {
  color: var(--text-main);
  letter-spacing: -.5px;
  margin-top: 2.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.cta-group {
  gap: 1rem;
  margin-top: 2.5rem;
  display: flex;
}

.btn-primary {
  background: var(--accent-primary);
  color: #000;
  border-radius: var(--radius-sm);
  border: 1px solid var(--accent-primary);
  text-transform: uppercase;
  letter-spacing: 1px;
  align-items: center;
  gap: .5rem;
  padding: 1rem 2.2rem;
  font-size: .9rem;
  font-weight: 700;
  text-decoration: none;
  transition: all .2s;
  display: inline-flex;
}

.btn-primary:hover {
  color: var(--accent-primary);
  box-shadow: 4px 4px 0 var(--accent-primary);
  background: none;
  transform: translate(-2px, -2px);
}

.btn-secondary {
  border: 1px solid var(--text-muted);
  color: var(--text-main);
  border-radius: var(--radius-sm);
  text-transform: uppercase;
  letter-spacing: 1px;
  background: none;
  align-items: center;
  gap: .5rem;
  padding: 1rem 2.2rem;
  font-size: .9rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .2s;
  display: inline-flex;
}

.btn-secondary:hover {
  border-color: var(--text-main);
  background: var(--bg-subtle);
  box-shadow: 4px 4px 0 var(--text-muted);
  transform: translate(-2px, -2px);
}

.btn-sm {
  padding: .6rem 1.2rem;
  font-size: .8rem;
}

.tech-stack-container {
  flex-wrap: wrap;
  gap: .5rem;
  margin: 1.5rem 0 3rem;
  display: flex;
}

.tech-tag {
  border: 1px solid var(--glass-border);
  color: var(--accent-primary);
  background: var(--bg-subtle);
  border-radius: var(--radius-sm);
  font-size: .85rem;
  font-weight: 500;
  font-family: var(--font-mono);
  padding: .5rem 1rem;
}

.tech-tag:hover {
  border-color: var(--accent-primary);
  background: var(--bg-core);
}

.glass-panel {
  background: var(--bg-card);
  -webkit-backdrop-filter: blur(20px);
  border: var(--glass-border);
  border-radius: var(--radius-md);
  box-shadow: 0 4px 20px var(--shadow-color);
  transition: all .2s;
}

.glass-panel:hover {
  border-color: var(--accent-primary);
  box-shadow: 6px 6px 0 var(--shadow-color);
  transform: translateY(-2px);
}

.bio-clean-container {
  border-left: 4px solid var(--accent-primary);
  background: var(--bg-subtle);
  border-radius: var(--radius-sm);
  margin-bottom: 3rem;
  padding: 2rem;
}

.code-block {
  border-radius: var(--radius-sm);
  font-family: var(--font-mono);
  color: var(--accent-primary);
  border: 1px solid var(--glass-border);
  background: #000;
  padding: 1.5rem;
  font-size: .9rem;
  overflow-x: auto;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th {
  text-align: left;
  color: var(--text-header);
  border-bottom: 2px solid var(--glass-border);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 1.2rem;
  font-size: .8rem;
  font-weight: 700;
}

td {
  color: var(--text-muted);
  border-bottom: 1px solid var(--glass-border);
  padding: 1.2rem;
  transition: background .2s;
}

tr:hover td {
  background: var(--bg-subtle);
  color: var(--text-main);
}

.hero-section {
  text-align: center;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  display: flex;
  position: relative;
}

.content-container {
  z-index: 2;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  display: flex;
}

.intro-text {
  font-family: var(--font-mono);
  color: var(--accent-primary);
  letter-spacing: 1px;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

.highlight-text {
  color: #000;
  background: var(--accent-primary);
  padding: 0 5px;
}

.main-headline {
  color: var(--text-header);
  letter-spacing: -2px;
  text-transform: uppercase;
  margin-bottom: 2rem;
  font-size: 4.5rem;
  font-weight: 900;
  line-height: 1;
}

.typing-effect {
  color: var(--accent-primary);
  border-right: 10px solid var(--accent-primary);
  padding-right: 5px;
  animation: .8s steps(2, end) infinite blinkCursor;
}

.sub-headline {
  color: var(--text-muted);
  max-width: 700px;
  margin-bottom: 3rem;
  padding-left: 0;
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.6;
}

@keyframes blinkCursor {
  0%, 100% {
    border-color: #0000;
  }

  50% {
    border-color: var(--accent-primary);
  }
}

.docs-footer-container {
  border-top: 1px solid var(--glass-border);
  margin-top: 5rem;
  padding-top: 3rem;
}

.docs-navigation {
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 4rem;
  display: flex;
}

.nav-card {
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-sm);
  background: var(--bg-subtle);
  flex-direction: column;
  flex: 1;
  padding: 1.5rem;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.nav-card:hover {
  border-color: var(--accent-primary);
  background: var(--bg-card);
  box-shadow: 4px 4px 0 var(--accent-primary);
  transform: translate(-2px, -2px);
}

.nav-label {
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 1px;
  align-items: center;
  gap: .5rem;
  margin-bottom: .6rem;
  font-size: .85rem;
  font-weight: 700;
  display: flex;
}

.nav-card.next {
  text-align: right;
  align-items: flex-end;
}

.nav-card.next .nav-label {
  justify-content: flex-end;
}

.nav-title {
  color: var(--text-main);
  font-size: 1.3rem;
  font-weight: 700;
}

.footer-main-heading {
  text-align: center;
  color: var(--text-header);
  margin-bottom: 3rem;
  font-size: 2rem;
}

.footer-grid {
  grid-template-columns: repeat(3, 1fr);
  align-items: stretch;
  gap: 1.5rem;
  margin-top: 1rem;
  display: grid;
}

.footer-section {
  text-align: center;
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  height: 100%;
  padding: 2rem;
  transition: all .2s;
}

.footer-section:hover {
  border-color: var(--accent-primary);
  background: #ffffff14;
}

.footer-subheading {
  color: var(--text-main);
  min-height: 2rem;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 700;
}

.feedback-buttons {
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.footer-links {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.contact-icon-btn {
  background: var(--bg-core);
  border: 1px solid var(--glass-border);
  width: 50px;
  height: 50px;
  color: var(--text-muted);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.contact-icon-btn:hover {
  background: var(--accent-primary);
  color: #000;
  border-color: var(--accent-primary);
  box-shadow: 3px 3px 0 var(--shadow-color);
  transform: translate(-2px, -2px);
}

.copyright-bar {
  border-top: 1px solid var(--glass-border);
  text-align: center;
  color: var(--text-muted);
  opacity: .6;
  margin-top: 4rem;
  padding-top: 2rem;
  font-size: .9rem;
}

.command-table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 2rem;
}

.command-table th {
  text-align: left;
  border-bottom: 2px solid var(--glass-border);
  color: var(--text-header);
  background: var(--bg-subtle);
  padding: 1rem;
  font-weight: 700;
}

.command-table td {
  border-bottom: 1px solid var(--glass-border);
  color: var(--text-muted);
  vertical-align: top;
  padding: 1rem;
}

.command-table tr:last-child td {
  border-bottom: none;
}

.command-table tr:hover td {
  background: var(--bg-subtle);
  color: var(--text-main);
}

.col-command {
  width: 30%;
  font-family: var(--font-mono);
  color: var(--accent-secondary);
  font-weight: 600;
}

.col-desc {
  width: 70%;
  line-height: 1.6;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: .4s forwards fadeIn;
}

.global-footer {
  border-top: 1px solid var(--glass-border);
  margin-top: 5rem;
  padding-top: 3rem;
  padding-bottom: 2rem;
}

.footer-link-small {
  color: var(--accent-primary);
  margin-top: 1rem;
  font-size: .8rem;
  text-decoration: none;
  display: block;
}

.footer-link-small:hover {
  text-decoration: underline;
}

.footer-links-vertical {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .8rem;
  display: flex;
}

.footer-link-icon {
  color: var(--accent-primary);
  align-items: center;
  gap: .5rem;
  font-size: .95rem;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.footer-link-icon:hover {
  text-decoration: underline;
}

.legal-links {
  color: var(--text-muted);
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  font-size: .8rem;
  display: flex;
}

.legal-links a {
  color: var(--text-muted);
  text-decoration: none;
}

.legal-links a:hover {
  color: var(--accent-primary);
  text-decoration: underline;
}

.language-selector {
  position: relative;
}

.lang-trigger {
  backdrop-filter: blur(15px);
  cursor: pointer;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  background: var(--bg-card) !important;
  border: 1px solid var(--glass-border) !important;
}

.lang-trigger:hover {
  box-shadow: 0 0 20px var(--shadow-hover);
  transform: translateY(-1px);
  border-color: var(--accent-primary) !important;
}

.lang-dropdown-overlay {
  z-index: 999;
  background: none;
  position: fixed;
  inset: 0;
}

.lang-dropdown {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  min-width: 200px;
  box-shadow: 0 8px 32px var(--shadow-color);
  z-index: 1000;
  animation: .2s cubic-bezier(.4, 0, .2, 1) slideDown;
  position: absolute;
  top: calc(100% + .5rem);
  right: 0;
  overflow: hidden;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.lang-option {
  border: none;
  border-bottom: 1px solid var(--glass-border);
  width: 100%;
  color: var(--text-main);
  font-family: var(--font-main);
  cursor: pointer;
  text-align: left;
  background: none;
  align-items: center;
  gap: .75rem;
  padding: .9rem 1.2rem;
  font-size: .9rem;
  transition: all .2s;
  display: flex;
}

.lang-option:last-child {
  border-bottom: none;
}

.lang-option:hover {
  background: var(--accent-primary);
  color: #000;
}

.lang-option.active {
  color: var(--accent-primary);
  background: #00ffcc1a;
  font-weight: 600;
}

.lang-flag {
  flex-shrink: 0;
  font-size: 1.3rem;
}

.lang-name {
  flex-grow: 1;
}

.check-mark {
  color: var(--accent-primary);
  font-size: 1.1rem;
  font-weight: bold;
}

.theme-light .lang-dropdown {
  background: #fffffff2;
  box-shadow: 0 8px 24px #0000001f;
}

.theme-light .lang-option:hover {
  background: var(--accent-primary);
  color: #fff;
}

.theme-light .lang-option.active {
  color: var(--accent-primary);
  background: #1a1a1a14;
}

.welcome-macos {
  z-index: 99999;
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.welcome-macos.fade-in {
  opacity: 1;
}

.macos-gradient-bg {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 25%, #f093fb 50%, #4facfe 75%, #00f2fe 100%);
  animation: 15s infinite gradientShift;
  position: absolute;
  inset: 0;
}

@keyframes gradientShift {
  0%, 100% {
    filter: hue-rotate();
  }

  50% {
    filter: hue-rotate(45deg);
  }
}

.orb {
  filter: blur(80px);
  opacity: .6;
  border-radius: 50%;
  animation: 20s ease-in-out infinite float;
  position: absolute;
}

.orb-1 {
  background: radial-gradient(circle, #0fc6 0%, #0000 70%);
  width: 400px;
  height: 400px;
  animation-delay: 0s;
  top: -10%;
  left: -10%;
}

.orb-2 {
  background: radial-gradient(circle, #667eea66 0%, #0000 70%);
  width: 500px;
  height: 500px;
  animation-delay: -7s;
  bottom: -15%;
  right: -15%;
}

.orb-3 {
  background: radial-gradient(circle, #f093fb4d 0%, #0000 70%);
  width: 350px;
  height: 350px;
  animation-delay: -14s;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes float {
  0%, 100% {
    transform: translate(0)scale(1);
  }

  25% {
    transform: translate(30px, -30px)scale(1.1);
  }

  50% {
    transform: translate(-20px, 20px)scale(.9);
  }

  75% {
    transform: translate(20px, 30px)scale(1.05);
  }
}

.welcome-card-macos {
  z-index: 10;
  text-align: center;
  -webkit-backdrop-filter: blur(40px) saturate(180%);
  background: #ffffff14;
  border: 1px solid #ffffff2e;
  border-radius: 24px;
  max-width: 480px;
  padding: 4rem 3rem;
  animation: .6s cubic-bezier(.4, 0, .2, 1) .2s both cardFadeIn;
  position: relative;
  box-shadow: 0 8px 32px #0003, inset 0 0 0 1px #ffffff1a;
}

@keyframes cardFadeIn {
  from {
    opacity: 0;
    transform: translateY(30px)scale(.95);
  }

  to {
    opacity: 1;
    transform: translateY(0)scale(1);
  }
}

.welcome-icon-macos {
  margin: 0 auto 2rem;
  animation: 3s ease-in-out infinite iconPulse;
}

@keyframes iconPulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }
}

.welcome-title-macos {
  -webkit-text-fill-color: transparent;
  letter-spacing: -1px;
  background: linear-gradient(135deg, #fff 0%, #fffc 100%) text;
  margin-bottom: .5rem;
  font-size: 3rem;
  font-weight: 700;
}

.welcome-subtitle-macos {
  color: #ffffffe6;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.welcome-desc-macos {
  color: #fff9;
  margin-bottom: 3rem;
  font-size: 1rem;
  font-weight: 400;
}

.welcome-button-macos {
  backdrop-filter: blur(10px);
  color: #fff;
  cursor: pointer;
  background: #ffffff26;
  border: 1px solid #fff3;
  border-radius: 12px;
  align-items: center;
  gap: .75rem;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  box-shadow: 0 4px 16px #0000001a;
}

.welcome-button-macos:hover {
  background: #ffffff40;
  border-color: #fff6;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #00000026;
}

.welcome-button-macos:active {
  transform: translateY(0);
}

.welcome-button-macos svg {
  transition: transform .3s;
}

.welcome-button-macos:hover svg {
  transform: translateX(4px);
}

.welcome-footnote-macos {
  color: #fff6;
  margin-top: 2rem;
  font-size: .875rem;
  font-weight: 400;
}

@media (width <= 600px) {
  .welcome-card-macos {
    margin: 1rem;
    padding: 3rem 2rem;
  }

  .welcome-title-macos {
    font-size: 2.25rem;
  }

  .welcome-subtitle-macos {
    font-size: 1.25rem;
  }
}

@media (width <= 900px) {
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .sidebar {
    width: 0;
    padding: 0;
    overflow: hidden;
  }

  .main-content {
    padding: 2rem;
    padding-top: calc(var(--header-height) + 2rem);
    margin-left: 0;
  }

  .main-headline {
    font-size: 2.5rem;
  }

  .main-header {
    padding: 0 1rem;
    left: 0;
  }

  .col-command {
    width: 40%;
  }

  .col-desc {
    width: 60%;
  }

  .language-selector {
    position: fixed;
    top: 1rem;
    right: 1rem;
  }
}
